const Receipt = () => {
  const [amount, setAmount] = useState(10)
  const [items, setItems] = useState([]);

  function handleGetItem() {
    const arr = getRandomItems(amount);
    setItems(arr);
  }

  function handleDownloadImage() {

    setAmount((v) => v - 1)

    const element = document.querySelector('.new-receipt');

    //  html2canvas  Canvas
    html2canvas(element, {
      scale: 1 //  scale 
    }).then(function (canvas) {
      //  Canvas  Blob 
      canvas.toBlob(function (blob) {
        //  URL 
        const imageUrl = URL.createObjectURL(blob);

        //  <a> 
        const link = document.createElement('a');
        link.href = imageUrl;
        link.download = `receipt.jpg`;
        link.click();

        //  URL 
        URL.revokeObjectURL(imageUrl);
      }, 'image/jpeg', 0.8); //  JPEG 
    });
  }

  useEffect(() => {
    handleGetItem()
  }, []);

  return (
    <div className="receipt-container" style={{position: "relative"}}>
      <div>
        <Button onClick={() => handleDownloadImage()} size="large"></Button>
      </div>
      <div id="receipt" className='new-receipt'>
        <ReceiptHeader/>
        <ReceiptItem items={items}/>
        <ReceiptFooter items={items}/>
      </div>
    </div>
  );
};

export default Receipt;

